@import url(https://fonts.googleapis.com/css?family=Work+Sans);
body {
  margin: 0;
  font-family: "Work Sans";
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #eceff1;
  color: #4d4d4d;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.PokeCard {
    width: 300px;
    padding: 10px;
    margin: 1rem;
    box-shadow: 7px 10px 12px -5px rgba(0, 0, 0, 0.56);
    text-align: center;
    background-color: whitesmoke;
    border-radius: 3px;
}

.pokecard-title {
    margin-bottom: 1rem;
    font-weight: bold;
    font-size: 1.5rem;
}

.Pokecard-data {
    font-size: 80%;
}

.Pokecard-image {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 250;
    background-color: #cdd0d1;
    border-radius: 3px;
    margin: 1rem;
}

.PokeCard img {
    transition: all ease-in500ms;
}

.PokeCard img:hover {
    -webkit-transform: scale(1.25);
            transform: scale(1.25);
    opacity: 0.7;
}
.Pokedex {
    text-align: center;
}

.Pokedex-cards {
    display: flex;
    justify-content: space-evenly;
    flex-wrap: wrap;
}

.Pokedex-winner {
    color: #4ca;
}

.Pokedex-loser {
    color: #e91e63;
}

